// export default checkIsDemoUser;

export type SaveFunction<T = void> = (args?: T | number | boolean) => void | Promise<void>;

const checkIsDemoUser = <T>(
  returnFunction: SaveFunction<T>,
  showSnackBar?: (message: string, severity?: 'success' | 'error' | 'warning' | 'info') => void
): SaveFunction<T> => {

  const credentialsData: any = JSON.parse(localStorage.getItem("Credentials"))
  
  return (args?: T): void | Promise<void> => {
    if (credentialsData?.isDemoUser) {
      showSnackBar("**Access Denied**\nWe're sorry, but you do not have permission to access this feature.");
    } else {
      returnFunction(args);
    }
  };
};

export default checkIsDemoUser;
