import { useRoutes } from 'react-router-dom';
import router from 'src/router';

import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';

import { CssBaseline } from '@mui/material';
import ThemeProviderWrapper from './theme/ThemeProvider';
import './App.css';

import { initializeApp } from "firebase/app";
// import firebaseConfig from 'src/firebase';
import { getMessaging, onMessage, getToken } from "firebase/messaging";
import { useEffect, useState } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import APIservice from './utils/APIservice';

function App() {
  const initialState = {
    apiKey: '',
    authDomain: '',
    projectId: '',
    storageBucket: '',
    messagingSenderId: '',
    appId: '',
    measurementId: '',
    vapidKey: ''
  };

  const content = useRoutes(router);
  const [notification, setNotification] = useState({ title: '', body: '' });
  let [jsonData, setJsonData] = useState(initialState);
  let [isShowLoad, setIsShowLoader] = useState(false);
  useEffect(() => {
    loadJson()
    // loadFirebaseJson()
    // let data = 
  }, [])

  const loadJson = async () => {
    try {
      const response = await fetch('/admin/variable.json');
      if (!response.ok) {
        console.log("File does not exist or is not accessible.");
        return;
      }
      const contentType = response.headers.get('content-type');
      if (!contentType || !contentType.includes('application/json')) {
        console.log("File does not exist or is not in JSON format.");
        return;
      }
      const res = await response.json();
      if (Object.keys(res).length > 0) {
        setJsonData(res);
        jsonData = res;
        const app = initializeApp(jsonData);
        requestPermission();
        listen();
      } else {
        console.log("File exists but has no data.");
      }
    } catch (error) {
      console.error("Error checking file data:", error);
    }
  }

  const loadFirebaseJson = async () => {
    try {
      const res = await APIservice.httpPost(
        '/api/admin/users/checkconfigfileexist',
        {},
      );
      if (res && res.status == 200) {
        if (res.recordList.private_key == '<YOUR-PRIVATE-KEY>') {
          setIsShowLoader(true);
          isShowLoad = true;
          sessionStorage.setItem("isShowLoader", JSON.stringify(isShowLoad))
        }
      }
    } catch (error) {
      console.error("Error checking file data:", error);
    }
  }

  const requestPermission = () => {
    const messaging = getMessaging();
    if (jsonData && jsonData.vapidKey) {
      getToken(messaging,
        { vapidKey: jsonData.vapidKey }).then(
          (currentToken) => {
            if (currentToken) {
              sessionStorage.setItem("fcmToken", currentToken);
              console.log(currentToken);

            } else {
              console.log('No registration token available. Request permission to generate one.');
            }
          }).catch((err) => {
            console.log('An error occurred while retrieving token. ', err);
          });
    }
  }

  const CustomToastWithLink = (title: string, description: string) => (
    <div>
      <div>
        {title}
      </div>
      <div>
        {description}
      </div>
    </div>
  );

  const listen = () => {
    const messaging = getMessaging();
    onMessage(messaging, (payload) => {
      toast.info(CustomToastWithLink(payload.notification.title, payload.notification.body), {
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: 'colored',
        position: toast.POSITION.TOP_RIGHT
      });
    });
  }

  return (
    <ThemeProviderWrapper>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <CssBaseline />
        {content}
      </LocalizationProvider>
    </ThemeProviderWrapper>
  );
}
export default App;
