import { useState } from 'react';
import { useTheme } from '@mui/material/styles';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
import ReactDOM from 'react-dom';

type SeverityType = 'success' | 'error' | 'warning' | 'info';

export const useSnackbar = () => {
  const [open, setOpen] = useState(false);
  const [message, setMessage] = useState('');

  const showSnackBar = (msg: string, severity: SeverityType = 'info') => {
    debugger;
    setMessage(msg);
    setOpen(true);
  };

  const theme = useTheme();

  const handleClose = () => {
    setOpen(false);
  };

  const SnackbarComponent = () => {
    return ReactDOM.createPortal(
      <Snackbar
        open={open}
        autoHideDuration={2000}
        onClose={handleClose}
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }} // Position at the top
        ContentProps={{
          sx: {
            zIndex: theme.zIndex.snackbar + 1, // Ensure it is higher than the dialog
            position: 'fixed' // Fixed positioning to keep it at the top of the viewport
          }
        }}
      >
        <Alert
          onClose={handleClose}
          severity="error"
          variant="filled"
          sx={{ width: '100%', marginTop: '60px', whiteSpace: 'pre-line' }} // Keep your original styles
        >
          {message}
        </Alert>
      </Snackbar>,
      document.body // Render outside of Dialog's DOM hierarchy
    );
  };

  return { showSnackBar, SnackbarComponent };
};
